.form-group {
  @include min-xxl {
    margin-bottom: 1.5em;
  }

  label {
    @include min-md {
      margin-bottom: 0; //消除reboot
    }
  }
}

.form-row {
  align-items: flex-start;
}

.form-control {
  @extend .fz-default; //覆蓋預設
  padding: 0 0.75rem;
  display: flex;
  align-items: center;
  background-color: transparent;
  border-radius: 0;

  @include min-md {
    border: none;
    padding-left: 0; //外面加上.form-border
  }

  &.custom-control-label {
    padding-left: 1.5em;
  }
}

.form-control:focus {
  color: $color-secondary !important; //會蓋不過bs
  border-color: $color-secondary;
  box-shadow: none;
}

///////////////////////
//  .custom-control
///////////////////////
$color-label: $color-text;

.custom-control-inline {
  display: inline-block;

  @include min-md {
    margin-right: 2em;
  }
}

.custom-control {
  padding-left: 0; //覆蓋預設
}

.custom-control-label {

  &:before, &:after {
    left: 0;
    top: .7rem;
    border-radius: 50px;

  }

  &:before {
    border-color: $color-label;
  }

  &:after {
    transform-origin: center;
    transform: scale(.5);
    background-color: $color-label;
    opacity: 0;
  }

}

//勾選時的狀態
.custom-control-input {
  cursor: pointer;
  height: 100%;
  top: 0;
  z-index: 1;

  //打勾時的狀態
  &:checked ~ .custom-control-label::before {
    border-color: $color-label;
    background-color: transparent;
  }

  //focus:打勾時的當下
  &:focus ~ .custom-control-label::before {
    box-shadow: none;
  }

  //focus:打勾時的當下
  &:focus:not(:checked) ~ .custom-control-label::before {
    border-color: $color-label;
    background-color: transparent;
  }

  //active:打勾時的當下
  &:not(:disabled):active ~ .custom-control-label::before {
    border-color: $color-label;
    background-color: transparent;
  }

  //&:disabled {
  //  //  ~ .custom-control-label {
  //  //    color: $custom-control-label-disabled-color;
  //  //
  //  //    &::before {
  //  //      background-color: $custom-control-indicator-disabled-bg;
  //  //    }
  //  //  }
  //  //}

}

.custom-radio,
.custom-checkbox {

  .custom-control-input {
    //打勾時的狀態
    &:checked ~ .custom-control-label::after {
      background-image: none;
      opacity: 1;
    }

  }

}

///////////////////////
//  radio
///////////////////////
.custom-radio {
  .custom-control-label {
    &:before {
      //border-radius: 0;
    }
  }
}

///////////////////////
//  checkbox
///////////////////////
.custom-checkbox {
  &.custom-control-inline {
    @include min-md {
      margin-right: 1em;
    }
  }

  .custom-control-label {
    &:before, &:after {
      border-radius: 0;
    }

    //打勾的樣式
    &:after {
      content: '\f00c';
      @extend %fontAwesome;
      top: .45em;
      transform: scale(1);
      background-color: transparent;
    }
  }
}

///////////////////////////////////////////////////////
//上傳檔案
.custom-file {
  display: flex;
  align-items: center;
  height: auto;
  margin-bottom: 1rem;
  border-bottom: 1px solid $custom-file-border-color;
  padding: 1em 0;

  @include min-sm {
    padding: 0;
  }
}

.custom-file-input,
.custom-file-label {
  padding: 0;
  height: 100%;

  @include min-sm {
    line-height: 70px;
  }
}

.custom-file-input {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  cursor: pointer;
}

.custom-file-label {
  position: static;
  left: auto;
  right: auto;
  top: 0;
  color: $color-text;
  border: none;
  border-radius: 0;

  @include min-sm {
    display: flex;
    align-items: center;
  }

  span {
    display: block;
  }

}

.custom-file-label::after {
  content: '上傳封面';
  color: $color-primary;
  height: auto;
  line-height: 2;
  padding: 0 1em;
  background-color: transparent;
  border: 1px solid $color-primary;
  border-radius: 4px;

  @include min-sm {
    top: 50%;
    bottom: inherit;
    transform: translateY(-50%);
  }
  @include max-sm {
    position: static;
    left: auto;
    right: auto;
    top: auto;
    bottom: auto;
    display: inline-block;
    margin-top: .5em;
  }
}

.custom-file-input:focus ~ .custom-file-label {
  border-color: $color-primary;
  box-shadow: none;
}

.custom-file-input:focus ~ .custom-file-label::after {
  border-color: $color-primary;
}


