$toggle-w: 45px;

//樣式動畫設定
.toggle {
  position: relative; //span
  display: block;
  width: $toggle-w;
  height: $toggle-w;
  transition: all .3s ease;

  span {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;

    width: 24px;
    height: 2px;
    background-color: $color-toggle;
    transition: background-color .15s .15s;

    &:before, &:after {
      content: '';
      display: block;

      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $color-toggle;

      transition-duration: .3s, .3s, .3s;
      transition-delay: 0s, 0s, 0s;
    }

    &:before {
      top: -6px;
      transition-property: top, transform, background-color;
    }

    &:after {
      bottom: -6px;
      transition-property: bottom, transform, background-color;
    }
  }

  &.show {

    span {
      background-color: transparent;
      transition: background-color .15s 0s;

      &:before, &:after {
        background: $color-toggle-active;
        transition-delay: 0s, .3s, 0s;
      }

      &:before {
        top: 0;
        transform: rotate(45deg);
      }

      &:after {
        bottom: 0;
        transform: rotate(-45deg);
      }

    }

  }

}

//位置定位
.js-toggle {
  position: fixed;
  right: 1em;
  top: ($header-Hxs) / 2;
  z-index: 1001;
  transform: translateY(-50%);
  transition: all .5s ease;
  background-color: #fff;
  border-radius: 50px;

  &.show {}

  @include min-xl {
    display: none;
  }
}


