main {
  >section {
    overflow: hidden;
  }
}

.secRow {
  display: flex;

  @include max-xl {
    flex-direction: column;
  }
  @include min-xl {
    .secCol {
      &:first-child {
        width: 40vw;
        max-width: 770px;
        flex-shrink: 0;
      }

      &:last-child {
        flex-grow: 1;
      }
    }
  }
}

//////////////////////////////
.flexPosBtm {
  @include min-xl {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
}

.flexPosBetween {
  @include min-xl {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

$spaceBtm-xs: 3.5rem;
$spaceBtm-lg: 4rem;
$spaceBtm-xl: 6rem;
.spaceBtm {
  padding-bottom: $spaceBtm-xs;
  @include min-lg {
    padding-bottom: $spaceBtm-lg;
  }
  @include min-xl {
    padding-bottom: $spaceBtm-xl;
  }
}

//網站灰色小標
.sub {
  display: block;
  color: $color-textL;
  @extend %font-title;
  @extend .fw-bold;
  @extend .fz-default;
  @extend %letterSpacing-md;
}

//////////////////////////////
.hero {
  height: 100vh;
  @extend %transition;
  padding-top: 160px;

  @include landscape-max(991px) {
    height: auto;

    .colorbox {
      min-height: 60vh;
    }
  }

  @include min-md {
    padding-top: 200px;
  }
  @include min-lg {
    padding-top: 160px;
  }
  @include min-xl {
    padding-top: 0;
  }

  &__wrap {
    @include max-xl {
      display: flex;
      flex-direction: column;
      height: 100%;
    }
  }

  &__row {
    @include max-xl {
      flex-grow: 1;
    }
    @include min-xl {
      height: 60vh;
    }
  }

  .herotitle {
    margin-bottom: .5em;

    @include min-xl {
      margin-bottom: 1.5em;
    }
  }

  @at-root .herotitle {
    @extend .fz-h3;
    @extend .fw-bold;
    @extend %letterSpacing-xl;
    text-align: right;
  }

  @at-root .hero_intro {

    @include max-xl {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;

      li {
        margin-right: 1em;
      }
    }

    li {
      &:before {
        content: '#';
      }
    }

    .hero_intro_item {
      @extend .fz-h6;
      margin-top: .5em;

      @include minmax(768px, 1199px) {
        margin-bottom: .5em;
      }

      @include min-xl {
        margin-top: 1em;
      }
    }
  }

  .hero_intro {
    overflow: hidden;

    @include minmax(992px, 1199px) {
      margin-bottom: -1em;
    }
  }

  .colorbox {
    overflow: hidden;
    @include max-xl {
      flex-grow: 1;
    }
  }

  .spaceBtm {
    @include max-md {
      padding-bottom: 2.5rem;
    }
  }
}

//////////////////////////////
.bio {
  $bioTopSpace: 20vh;

  @at-root .bioHero {
    @extend .fz-h5;
    @extend .fw-bold;
    line-height: 1.5;
    @extend %letterSpacing-lg;

    span {
      display: block;
    }

    .name {
      @extend .fz-h6;
      @extend .fw-regular;
      line-height: 1;
      letter-spacing: 0;
      margin-left: .1em;
      margin-bottom: .75em;
    }
  }

  @at-root .bioTech {
    &__item {
      margin-top: 2.5em;
      margin-bottom: .5em;

      @include min-lg {
        margin-top: 3em;
      }

      &:first-child {
        @include min-xl {
          margin-top: 0;
        }
      }

      & + .bioTech__item {
        @include min-xl {
          margin-top: 4em;
        }
      }
    }

    &__list {
      display: flex;
      flex-wrap: wrap;
    }

    &__list_item {
      @extend .fz-default;
      @extend .fw-medium;
      line-height: 1.2;
      margin-top: 1.5em !important;
      display: flex;
      flex-wrap: wrap;
      position: relative; //before

      @include min-md {
        margin-top: 2em !important;
      }

      &:last-child {
        &:after {
          display: none;
        }
      }

      &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: $color-title;
      }

      &:after {
        content: '/';
        margin: 0 1em;
      }
    }
  }

  .bioIntro {
    padding: $spaceBtm-xs 0;

    @include min-lg {
      padding: $spaceBtm-lg 0;
    }
    @include min-xl {
      height: 100vh;
    }

    @include min-xxxl {
      padding: $spaceBtm-xl 0;
    }
  }

  .bioTech {
    @include min-xxxl {
      width: 80%;
    }
  }

  .bioInfo {
    margin-top: $spaceBtm-xs;
    margin-bottom: $spaceBtm-xs;

    @include min-lg {
      margin-top: $spaceBtm-lg;
      margin-bottom: $spaceBtm-lg;
    }

    @include min-xl {
      max-width: 750px;
      margin-left: auto;
      margin-right: auto;
    }

    @include min-xxxl {
      margin-top: $spaceBtm-xl;
      margin-bottom: $spaceBtm-xl;
    }
  }

  @at-root .goal {
    .txt {
      @extend .fz-max;
      @extend .fw-bold;
      @extend %letterSpacing-xl;
      line-height: 2;
    }
  }

  @at-root .serve {
    margin-top: 4em;

    @include min-xl {
      margin-top: 7em;
    }

    @include min-xxl {
      margin-top: 8em;
    }

    & + .serve {
      border-top: 1px solid $color-border;

      margin-top: 3em;
      padding-top: 3.5em;

      @include min-xxl {
        margin-top: 4em;
        padding-top: 4.5em;
      }
    }

    .name {
      @extend .fz-max;
      @extend .fw-medium;
      @extend %letterSpacing-md;
    }

    .position {
      @extend .fz-default;
      @extend %letterSpacing-sm;
      margin-top: .75em;
    }

    .txt {
      color: $color-text;
      @extend .fz-large;
      @extend %letterSpacing-sm;
      line-height: 2.3;
    }

    .period {
      display: block;
      color: $color-textL;
      @extend .fz-default;
      @extend .fw-bold;
      @extend %font-title;
      margin-top: 2em;
      letter-spacing: .05em;

      @include min-md {
        letter-spacing: .15em;
      }
    }
  }
}

//////////////////////////////
//水平捲動
.horizontal {
  white-space: nowrap;
  font-size: 0;

  .horizontal__item {
    font-size: initial;
    width: 1000px; //指定寬度
    height: 100vh;
    display: inline-block;
    vertical-align: top;
    white-space: normal;

    $translateX: 0;

    &:nth-of-type(even) {
      .card {
        transform: translate(#{$translateX}, -5vw);
      }
    }

    &:nth-of-type(odd) {
      .card {
        transform: translate(#{$translateX}, 5vw);
      }
    }

    .inner {
      height: 100%;
      display: flex;
      align-items: center;
    }

    //需要明確的寬度，不能用vw，不然會捲軸會判斷錯誤
    &.projectHero {
      //width: 40vw;
      //max-width: 770px;

      position: relative; //before
      z-index: 0;

      &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: -1;
        width: 40vw;
        max-width: 770px;
        height: 100%;
        background-color: #E7E7E6;
      }

      &:after {
        display: none;
      }

      .slogan {
        @include min-xl {
          position: relative;
          left: 8em;
        }
      }
    }
  }
}

.project {
  background-color: #718B8B;

  position: relative; //after

  &:after {
    content: '';
    display: block;

    width: 100%;
    height: 50px;
    background-color: #A99494;

    @include min-xl {
      position: absolute;
      left: 0;
      bottom: 0;
      height: 10%;
      max-height: 60px;
    }
  }

  &__list {
    @include max-xl {
      display: flex;
      overflow: hidden;
      overflow-x: auto;

      .project__list_item {
        margin: 4em 0 4em #{$wrapfluid};

        &:last-child {
          padding-right: $wrapfluid;
        }

        .inner {
          text-align: center;
        }

        .subtitle {
          text-align: left;
        }
      }
    }

    @include max-md {
      .project__list_item {
        margin: 3em 0 3em 6vw;

        &:first-child {
          margin-left: #{$wrapfluid};
        }
      }
    }
  }

  &__list_item {

    &:nth-of-type(even) {
      .card {

        .title {

        }

        .subtitle {
          //bottom: -.425em;
          bottom: -1em;
        }
      }
    }

    &:nth-of-type(odd) {
      .card {
        &__group {
          @include min-xl {
            align-items: flex-start;
          }
        }

        .subtitle {
          //top: -.575em;
          top: -1.25em;
        }
      }
    }
  }

  @at-root .projectHero {}

  @at-root .slogan {
    @extend .fz-h3;
    line-height: 1.6;
    text-decoration: underline;
  }
}

//////////////////////////////
.case {
  padding: 12vw 0;

  @include min-md {
    padding: 8vw 0;
  }

  .title {
    @extend .fz-h5;
  }

  .subtitle {
    @extend .fz-large;
    margin-bottom: 2em;
  }

  &__row {
    margin-top: 1em;

    @include min-md {
      margin-top: 4em;

      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    @include min-xl {
      margin-top: 6em;
    }
  }

  &__col {

    margin-top: 3em;

    @include min-md {
      flex-grow: 1;
      //width: 45%;

      &:last-child {
        width: 100%;
      }
    }

    @include min-lg {
      //width: 30%;
      max-width: 40%;
      margin-right: 5%;

      &:last-child {
        width: 25%;
        max-width: 350px;
        flex-shrink: 0;
        margin-right: 0;
        text-align: right;
      }
    }

  }

  @at-root .caseList {
    a {
      display: block;
      padding: 1.2em 0 1em 0;
      @extend .fz-small;
      @extend .fw-medium;
      position: relative; //after

      &:after {
        content: '';
        display: block;
        position: absolute;
        right: 2em;
        top: 50%;
        width: 8px;
        height: 8px;
        border-top: 1px solid rgba($color-title, .8);
        border-right: 1px solid rgba($color-title, .8);
        transform: rotate(45deg) translateY(-50%);
        opacity: 0;
        @extend %transition;
      }

      &:hover {
        &:after {
          opacity: 1;
          right: .5em;
        }
      }
    }

    &__list {
    }

    &__list_item {
      border-top: 1px solid $color-title;

      &:last-child {
        border-bottom: 1px solid $color-title;
      }
    }
  }

  .contact {
    display: inline-block;
    text-align: left;

    .txt {
      @extend .fz-max;
      @extend .fw-bold;
      line-height: 2.25;
      @extend %letterSpacing-xl;
    }

    &__list {
      margin-top: 3em;
    }

    &__list_item {
      margin-top: .5em;

      &:last-child {
        margin-top: 1.5em;

        a {
          text-decoration: none;
        }
      }

    }

    a {
      display: block;
      @extend .fz-default;
      letter-spacing: .1em;
      text-decoration: underline;
    }

    i {
      display: block;
      font-size: 30px;
    }
  }

}


