@charset "utf-8";

//此單元為前端共用規則，請依照此規則切版
////////////////////////////////////////////////////
//                                                //
//                    //font//                    //
//                                                //
////////////////////////////////////////////////////

//預設字型 - 字體分別是Linux系統的文泉驛正黑，Mac的蘋方體，微软雅黑,微軟正黑體，英文，都沒有執行的字體
$font-all: 'Lexend Deca', 'Verdana', 'Noto Sans TC', "WenQuanYi Zen Hei", "PingFang", "Microsoft JhengHei", "Microsoft YaHei", sans-serif;
$font-title: 'Questrial', 'Noto Sans TC', sans-serif;

%font-title {
  font-family: $font-title;
}


////////////////////////////////////////////////////
//                                                //
//                   //color//                    //
//                                                //
////////////////////////////////////////////////////

//lighten(red, 50%) 增亮50%
//darken(blue, 50%) 變暗50%s

//第一色系
$color-primary: #CC3C33;
//$color-primaryD: darken($color-primary, 20%); //未使用
//$color-primaryL: lighten($color-primary, 20%); //未使用

//第二色系
$color-secondary: #F88068;
//$color-secondaryD: darken($color-secondary, 20%); //未使用
//$color-secondaryL: lighten($color-secondary, 20%); //未使用

$color-third: #5CAFCA;

//字體顏色
$color-title: #000;

$color-text: #7F7F7F;
$color-textL: #B2B2B2;

//線條顏色
$color-border: #CCCCCC;
$color-borderL: #EEEEEE;
$color-borderD: #d9d9d9;

$color-table: $color-borderD;

//如需增加class，於下方新增
$fontcolors: (
        //"primary": $color-primary,
        //"secondary": $color-secondary,
        "title": $color-title,
        "text": $color-text,
        "textL": $color-textL,
);

@each $name, $color in $fontcolors {
  .fc-#{$name} {
    color: $color !important;
  }
}

//////////////////////
//font-weight
//以思源黑體為基礎
///////////////////////
$fontWeight: (
        "thin": 100,
        "light": 300,
        "regular": 400,
        "medium": 500,
        "semibold": 600,
        "bold": 700,
        "black": 900,
);

@each $fwname, $fwValue in $fontWeight {
  .fw-#{$fwname} {
    font-weight: $fwValue;
  }

  .fw-i-#{$fwname} {
    font-weight: $fwValue !important;
  }
}


