//字母間距
//.02
%letterSpacing-xs {
  letter-spacing: .025em;
}

//.05
%letterSpacing-sm {
  letter-spacing: .05em;
}

//.15
%letterSpacing-md {
  letter-spacing: .15em;
}

//.25
%letterSpacing-lg {
  letter-spacing: .25em;
}

//.5
%letterSpacing-xl {
  letter-spacing: .5em;
}

//////////////////////////////
.cursor {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 200;
  pointer-events: none;
  transition: width .3s, height .3s;
  background-color: $color-secondary;
  border-radius: 100%;

  &:before,
  &:after {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: $color-secondary;
    transform: translate(-50%, -50%);
    opacity: 0;
  }

  $w: 50%;
  $h: 2px;

  &:before {
    width: $w;
    height: $h;
  }

  &:after {
    width: $h;
    height: $w;
  }

  &.links_area {
    border: 2px solid $color-secondary;
    background-color: transparent;

    &:before,
    &:after {
      opacity: 1;
    }
  }
}

//////////////////////////////
#top {
  position: fixed;
  right: 5vw;
  bottom: 50px;
  z-index: 100;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  background-color: #000;
  border-radius: 100%;
  //border-radius: 37% 63% 49% 51% / 48% 42% 58% 52%;
  transition: all .3s ease;

  @include min-xl {
    right: 45px;
    bottom: 45px;
    width: 55px;
    height: 55px;

    @include hover {
      margin-bottom: .5em;
      background-color: $color-primary;
      //border-radius: 56% 44% 42% 58% / 54% 50% 50% 46%;
    }
  }

  .arrow {
    display: block;
    width: 10px;
    height: 10px;
    border-right: 2px solid #fff;
    border-top: 2px solid #fff;
    transform: rotate(-45deg);
    margin-top: .25em;
  }
}

//////////////////////////////
.colorbox {
  position: relative; //colorbox_item

  .colorbox_item {
    position: absolute;
    z-index: -1;
  }
}

.colorbox_01 {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #F5EBCD;
}

.colorbox_02 {
  left: 40%;
  top: 0;
  width: 60%;
  height: 100%;
  background-color: #5CAFCA;
}

.colorbox_03 {
  left: 20%;
  top: 65%;
  width: 50%;
  height: 100%;
  background-color: #F88068;

  @include minmax(0px, 767px) {
    top: 48%;
  }
  @include minmax(992px, 1199px) {
    top: 50%;
  }

}

.colorbox_04 {
  left: 70%;
  top: 40%;
  width: 30%;
  height: 100%;
  background-color: #D4F0A8;

  @include minmax(0px, 1199px) {
    top: 30%;
  }
}

.colorbox_05 {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #FAFAFA;
}

.colorbox_06 {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 10%;
  max-height: 80px;
  background-color: #A99494;
}

