//////////////////////////////
.card {
  display: block;
  color: #fff;
  position: relative; //*
  
  @include hover {
    color: #fff;
  }

  &__group {
    display: flex;
    align-items: flex-end;
  }

  .imgbox {
    width: 80vw;
    height: 50vw;

    background-color: #fff;

    @include min-md {
      width: 70vw;
      height: 43vw;
    }

    @include min-lg {
      width: 50vw;
      height: 31.5vw;
      max-width: 650px;
      max-height: 400px;
    }
  }

  .subtitle {
    font-size: 35px;
    line-height: 1;
    margin-top: .25em;

    @include min-sm {
      font-size: 40px;
    }

    @include min-md {
      font-size: 55px;
    }

    @include min-xl {
      font-size: 90px;
      white-space: nowrap;
      position: absolute; //偏移
      right: -.5em;
      margin-top: 0;
    }

    @include min-xxl {
      font-size: 100px;
    }
  }

  .title {
    @extend .fz-max;
    @extend .fw-bold;
    @extend .verticallr;
    letter-spacing: .3em;
    margin-bottom: -.3em;
    margin-left: .5em;

    @include min-xl {
      margin-left: .75em;
    }
  }

}

//////////////////////////////

.marquee {
  height: 4em;
  position: relative; //.box
  top: 1.5em;

  @include min-md {
    top: 2em;
    height: 5em;
  }

  &_box {
    white-space: nowrap;
    width: auto;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding-left: 100%;
    animation: marqee 12s infinite linear;

    @include min-md {
      animation: marqee 20s infinite linear;
    }
  }

  .running_text {
    white-space: nowrap;
    display: inline-block;

    & + .running_text {
      padding-left: 10%;
    }
  }
}

@keyframes marqee {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-100%);
  }
}

//////////////////////////////
footer {
  @extend %letterSpacing-sm;
}


