.mainHeader {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;

  @at-root .logo {
    display: block;
    @extend .fz-h1;
    @extend %font-title;
    line-height: 1;
    letter-spacing: .01em;
    margin-top: .3em;

    @include hover {
      color: $color-title;
    }
  }
}


