//////////////////////
//grid 檢測
//////////////////////
.grid {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 9999;
  pointer-events: none;
  opacity: 0;
  @extend %transition;
  * {
    height: 100%;
  }
  .row {
    //@extend .no-gutters;
  }
  .in {
    background: #82f5ff;
    opacity: .2;
  }
  &.active {
    opacity: 1;
  }
  
  .col {
    &:nth-of-type(2n) {
      .in {
        //background-color: #ff0000;
        //opacity: .2;
      }
    }
  }
}

.gridbtn {
  cursor: pointer;
  position: fixed;
  right: 0;
  z-index: 99999;

  height: 50px;
  padding: 1em;
  color: #fff;
  background: red;
  //opacity: .5;
}

.text-trigger-btn {
  top: 30%;
}

.text-trigger-btn-right {
  top: 40%;
}

.text-trigger-btn-left {
  top: 50%;
}

.text-trigger-btn-fluid {
  top: 60%;
}

.text-trigger-btn-fluidr {
  top: 70%;
}




