//整個網站預設字型大小
$base-font-size: 16px;
$fz-base: 16;
$fz-base-lg: 15;


// 文字樣式
$fz-max: #{20/$fz-base}rem;
$fz-large: #{17/$fz-base}rem;

$fz-small: #{15/$fz-base}rem;
$fz-min: #{14/$fz-base}rem;
//$fz-extramin: #{11/$fz-base}rem;

//title樣式
$fz-h1: #{180/$fz-base}rem;//60
$fz-h2: #{100/$fz-base}rem;//56
$fz-h3: #{50/$fz-base}rem;//54
$fz-h4: #{45/$fz-base}rem;//40
$fz-h5: #{40/$fz-base}rem;//30
$fz-h6: #{22/$fz-base}rem;//28

$fz-default: #{16/$fz-base}rem;

//平版樣式
// 文字樣式
$fzsm-max: #{20/$fz-base}rem;
$fzsm-large: #{18/$fz-base}rem;

$fzsm-small: #{15.5/$fz-base}rem;
$fzsm-min: #{15/$fz-base}rem;
//$fzsm-extramin: #{11/$fz-base}rem;

//title樣式
$fzsm-h1: #{120/$fz-base}rem;
$fzsm-h2: #{65/$fz-base}rem;
$fzsm-h3: #{42/$fz-base}rem;
$fzsm-h4: #{38/$fz-base}rem;
$fzsm-h5: #{32/$fz-base}rem;
$fzsm-h6: #{21/$fz-base}rem;

$fzsm-default: #{16/$fz-base}rem;

//手機版樣式
// 文字樣式
$fzxs-max: #{19/$fz-base}rem;
$fzxs-large: #{17/$fz-base}rem;

$fzxs-small: #{15/$fz-base}rem;
$fzxs-min: #{14.5/$fz-base}rem;
//$fzxs-extramin: #{11/$fz-base}rem;


//title樣式
$fzxs-h1: #{100/$fz-base}rem;
$fzxs-h2: #{60/$fz-base}rem;
$fzxs-h3: #{33/$fz-base}rem;
$fzxs-h4: #{30/$fz-base}rem;
$fzxs-h5: #{28/$fz-base}rem; //ok
$fzxs-h6: #{20/$fz-base}rem;

$fzxs-default: #{15.5/$fz-base}rem;


// 文字樣式
.fz-large {
  font-size: $fz-large;
}

.fz-max {
  font-size: $fz-max;
}

.fz-small {
  font-size: $fz-small;
}

.fz-min {
  font-size: $fz-min;
}

//.fz-extramin {
//  font-size: $fz-extramin;
//}

//title樣式
.fz-h1 {
  font-size: $fz-h1;
}

.fz-h2 {
  font-size: $fz-h2;
}

.fz-h3 {
  font-size: $fz-h3;
}

.fz-h4 {
  font-size: $fz-h4;
}

.fz-h5 {
  font-size: $fz-h5;
}

.fz-h6 {
  font-size: $fz-h6;
}

.fz-default {
  font-size: $fz-default;
}

//平版樣式
//1200↓
@include max-xl {

  .fz-large {
    font-size: $fzsm-large;
  }

  .fz-max {
    font-size: $fzsm-max;
  }

  .fz-small {
    font-size: $fzsm-small;
  }

  .fz-min {
    font-size: $fzsm-min;
  }

  //.fz-extramin {
  //  font-size: $fzsm-extramin;
  //}

  //title樣式
  .fz-h1 {
    font-size: $fzsm-h1;
  }

  .fz-h2 {
    font-size: $fzsm-h2;
  }

  .fz-h3 {
    font-size: $fzsm-h3;
  }

  .fz-h4 {
    font-size: $fzsm-h4;
  }

  .fz-h5 {
    font-size: $fzsm-h5;
  }

  .fz-h6 {
    font-size: $fzsm-h6;
  }

  .fz-default {
    font-size: $fzsm-default;
  }
}

//手機版樣式
//768↓
@include max-md {
  .fz-large {
    font-size: $fzxs-large;
  }

  .fz-max {
    font-size: $fzxs-max;
  }

  .fz-small {
    font-size: $fzxs-small;
  }

  .fz-min {
    font-size: $fzxs-min;
  }

  //.fz-extramin {
  //  font-size: $fzxs-extramin;
  //}

  //title樣式
  .fz-h1 {
    font-size: $fzxs-h1;
  }

  .fz-h2 {
    font-size: $fzxs-h2;
  }

  .fz-h3 {
    font-size: $fzxs-h3;
  }

  .fz-h4 {
    font-size: $fzxs-h4;
  }

  .fz-h5 {
    font-size: $fzxs-h5;
  }

  .fz-h6 {
    font-size: $fzxs-h6;
  }

  .fz-default {
    font-size: $fzxs-default;
  }
}


//轉換font-size 為 rem用法： @include font-size(16px)
@function calculateRem($size) {
  $remSize: $size / 16px;
  @return $remSize * 1rem;
}

@mixin font-size($size) {
  font-size: $size;
  font-size: calculateRem($size);
}


