%font-all {
  font-family: $font-all;
}

%fontAwesome {
  font-family: "Font Awesome 5 Free", "Font Awesome 5 Brands" !important; //fb需要使用brands
  font-weight: 900; //需跟Font Awesome 5搭配
}

%transition {
  transition: all 0.3s ease;
}

%transition-slow {
  transition: all 1s ease;
}

%transition-none {
  transition: none;
}

//文字超過出現點點點
%textflowell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

//取消文字超過出現點點點
%textflowclip {
  overflow: visible;
  text-overflow: clip;
  white-space: normal;
}

//清除select下拉樣式
%select {
  appearance: none;
  border-radius: 0;
  background-position-y: 50%;
  background-position-x: calc(100% - 15px);
  background-repeat: no-repeat;
  &::-ms-expand {
    display: none;
  }
}

.v-text, %v-text {
  -ms-writing-mode: tb-rl; /* old syntax. IE */
  -webkit-writing-mode: vertical-rl;
  -moz-writing-mode: vertical-rl;
  -ms-writing-mode: vertical-rl;
  writing-mode: vertical-rl; /* new syntax */
  display: inline-block;
}

.fixed-bg, %fixed-bg {
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  -ms-background-size: cover;
  -o-background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  background-size: cover;
}

.cover-bg, %cover-bg {
  background-position: center;
  background-repeat: no-repeat;
  -ms-background-size: cover;
  -o-background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  background-size: cover;
}

.contain-bg, %contain-bg {
  background-size: contain;
  background-repeat: no-repeat;
}

////////////////////////////
%font-light {
  font-weight: 300;
}

//ul li 樣式設定
%no-desc {
  li {
    list-style: none;
    margin: 0;
    padding-left: 0;
    display: block;

    &:before {
      display: none;
    }
  }
}

%no-desc-li {
  list-style: none;
  margin: 0;
  padding-left: 0;
  display: block;

  &:before {
    display: none;
  }
}

//取消ul li樣式設定
%desc-default {
  li {
    list-style: none;
    padding-left: 0;
    margin: 0;

    &:before {
      content: '';
    }
  }
}

//取消ul li樣式設定，顯示data-th
%desc-default-pseudo {
  li {
    list-style: none;
    padding-left: 0;
    position: static;
    margin: 0;

    &:before {
      content: attr(data-th)' :';
      position: static;
      left: auto;
      top: auto;
      transform: none;
      width: auto;
      height: auto;
      background: transparent;
      margin-right: .5em;
    }
  }
}

//ul li 樣式設定
%pseudo-absolute {
  content: '';
  display: block;
  position: absolute;
}

%pseudo-absolute-top {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

%pseudo-absolute-btm {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

%pseudo-absolute-vmiddle {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

%absolute-vmiddle {
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

%absolute-full {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}


