/* 網站基礎設定
==================================*/
* {
  padding: 0;
  margin: 0;
}

html, body {
  font-size: 16px;
}

body {
  margin: 0 auto;
  color: #000;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (min-width: 1200px) {
  body {
    line-height: 2;
  }
}

body ::selection {
  background: #CC3C33;
  color: #fff;
}

body img::selection {
  background: transparent;
}

*:focus {
  outline: none;
}

a {
  color: inherit;
  transition: all .3s ease;
}

a:hover {
  color: #CC3C33;
}

u {
  text-decoration: none;
  color: #CC3C33;
  min-width: 5px;
  display: inline-block;
  padding: 2px 0 0 5px;
  margin: 0;
  line-height: 100%;
  vertical-align: text-top;
}

b,
strong {
  display: inline-block;
}

ul > li {
  list-style: none;
  display: table;
  table-layout: fixed;
}

ul > li:before {
  content: '・';
  display: table-cell;
  vertical-align: top;
}

@media (min-width: 1200px) {
  ul > li:before {
    width: 1em;
  }
}

ul > li img {
  vertical-align: middle;
}

ol {
  margin-left: 1.5em;
}

ol li {
  list-style-type: cjk-ideographic;
}

ol img {
  vertical-align: middle;
}

ol ul {
  margin: 1em 0;
}

ol ul li {
  list-style-type: none;
}

li + li {
  margin-top: .5em;
}

li > p {
  margin-top: 0;
}

hr {
  border: 0;
  height: 1px;
  background-color: #CCCCCC;
  color: #d4d4d4;
  /* IE6 */
  margin-top: 10px;
  margin-bottom: 10px;
}

p + p {
  margin-top: .5em;
}

* + p {
  margin-top: 1em;
}

table {
  max-width: 100%;
}

table th, table td {
  padding: 1em 1em .85em 1em;
  border: 1px solid #d9d9d9;
}

hr {
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

@media (min-width: 1200px) {
  hr {
    margin-top: 2.5em;
    margin-bottom: 2.5em;
  }
}

blockquote {
  border-left: 5px solid #CC3C33;
  padding-left: 1.5em;
}

@media (min-width: 1200px) {
  blockquote {
    padding-left: 2em;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  line-height: 1.3;
  margin: 0;
}

@media (min-width: 768px) {
  h1, h2, h3, h4, h5, h6 {
    line-height: 1.2;
  }
}

main {
  overflow: hidden;
}

.wrap {
  width: 88%;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .wrap {
    width: 90%;
  }
}

.wrap {
  max-width: 1400px;
}

.wrap-cus-l, .wrap-cus-r {
  width: calc((100% - 88%) / 2 + 88%);
}

@media (min-width: 768px) {
  .wrap-cus-l, .wrap-cus-r {
    width: calc((100% - 90%) / 2 + 90%);
  }
}

.wrap-cus-l, .wrap-cus-r {
  max-width: calc((100% - 1400px) / 2 + 1400px);
}

.wrap-cus-l {
  margin-right: auto;
}

.wrap-cus-r {
  margin-left: auto;
}

.wrap-cus-l-max-xl {
  margin-right: auto;
  margin-left: unset;
}

@media (max-width: 1199px) {
  .wrap-cus-l-max-xl {
    width: calc((100% - 90%) / 2 + 90%);
  }
}

@media (max-width: 767px) {
  .wrap-cus-l-max-xl {
    width: calc((100% - 88%) / 2 + 88%);
  }
}

.wrap-cus-r-max-xl {
  margin-left: auto;
  margin-right: unset;
}

@media (max-width: 1199px) {
  .wrap-cus-r-max-xl {
    width: calc((100% - 90%) / 2 + 90%);
  }
}

@media (max-width: 767px) {
  .wrap-cus-r-max-xl {
    width: calc((100% - 88%) / 2 + 88%);
  }
}

.wrap-fluid {
  width: calc(100% - 5vw * 2);
  margin-left: auto;
  margin-right: auto;
}

.wrap-fluid-cus-l {
  width: calc(100% - 5vw * 1.5);
  margin-right: auto;
}

.wrap-fluid-cus-r {
  width: calc(100% - 5vw * 1.5);
  margin-left: auto;
}

@media (max-width: 1199px) {
  .wrap-max-xl-visible {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 991px) {
  .wrap-max-lg-visible {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 767px) {
  .wrap-max-md-visible {
    width: 88%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 575px) {
  .wrap-max-sm-visible {
    width: 88%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 767px) {
  .wrap-max-xl-visible, .wrap-max-lg-visible {
    width: 88%;
  }
}

@media (max-width: 575px) {
  .w100-max-sm {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .w100-max-md {
    width: 100%;
  }
}

@media (max-width: 991px) {
  .w100-max-lg {
    width: 100%;
  }
}

@media (max-width: 1199px) {
  .w100-max-xl {
    width: 100%;
  }
}

@media (max-width: 1449px) {
  .w100-max-xxl {
    width: 100%;
  }
}

.js-fillbg {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.mainHeader {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.logo {
  display: block;
  line-height: 1;
  letter-spacing: .01em;
  margin-top: .3em;
}

.logo:hover {
  color: #000;
}

@media (min-width: 1450px) {
  .form-group {
    margin-bottom: 1.5em;
  }
}

@media (min-width: 768px) {
  .form-group label {
    margin-bottom: 0;
  }
}

.form-row {
  align-items: flex-start;
}

.form-control {
  padding: 0 0.75rem;
  display: flex;
  align-items: center;
  background-color: transparent;
  border-radius: 0;
}

@media (min-width: 768px) {
  .form-control {
    border: none;
    padding-left: 0;
  }
}

.form-control.custom-control-label {
  padding-left: 1.5em;
}

.form-control:focus {
  color: #F88068 !important;
  border-color: #F88068;
  box-shadow: none;
}

.custom-control-inline {
  display: inline-block;
}

@media (min-width: 768px) {
  .custom-control-inline {
    margin-right: 2em;
  }
}

.custom-control {
  padding-left: 0;
}

.custom-control-label:before, .custom-control-label:after {
  left: 0;
  top: .7rem;
  border-radius: 50px;
}

.custom-control-label:before {
  border-color: #7F7F7F;
}

.custom-control-label:after {
  transform-origin: center;
  transform: scale(0.5);
  background-color: #7F7F7F;
  opacity: 0;
}

.custom-control-input {
  cursor: pointer;
  height: 100%;
  top: 0;
  z-index: 1;
}

.custom-control-input:checked ~ .custom-control-label::before {
  border-color: #7F7F7F;
  background-color: transparent;
}

.custom-control-input:focus ~ .custom-control-label::before {
  box-shadow: none;
}

.custom-control-input:focus:not(:checked) ~ .custom-control-label::before {
  border-color: #7F7F7F;
  background-color: transparent;
}

.custom-control-input:not(:disabled):active ~ .custom-control-label::before {
  border-color: #7F7F7F;
  background-color: transparent;
}

.custom-radio .custom-control-input:checked ~ .custom-control-label::after,
.custom-checkbox .custom-control-input:checked ~ .custom-control-label::after {
  background-image: none;
  opacity: 1;
}

@media (min-width: 768px) {
  .custom-checkbox.custom-control-inline {
    margin-right: 1em;
  }
}

.custom-checkbox .custom-control-label:before, .custom-checkbox .custom-control-label:after {
  border-radius: 0;
}

.custom-checkbox .custom-control-label:after {
  content: '\f00c';
  top: .45em;
  transform: scale(1);
  background-color: transparent;
}

.custom-file {
  display: flex;
  align-items: center;
  height: auto;
  margin-bottom: 1rem;
  border-bottom: 1px solid #ced4da;
  padding: 1em 0;
}

@media (min-width: 576px) {
  .custom-file {
    padding: 0;
  }
}

.custom-file-input,
.custom-file-label {
  padding: 0;
  height: 100%;
}

@media (min-width: 576px) {
  .custom-file-input,
  .custom-file-label {
    line-height: 70px;
  }
}

.custom-file-input {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  cursor: pointer;
}

.custom-file-label {
  position: static;
  left: auto;
  right: auto;
  top: 0;
  color: #7F7F7F;
  border: none;
  border-radius: 0;
}

@media (min-width: 576px) {
  .custom-file-label {
    display: flex;
    align-items: center;
  }
}

.custom-file-label span {
  display: block;
}

.custom-file-label::after {
  content: '上傳封面';
  color: #CC3C33;
  height: auto;
  line-height: 2;
  padding: 0 1em;
  background-color: transparent;
  border: 1px solid #CC3C33;
  border-radius: 4px;
}

@media (min-width: 576px) {
  .custom-file-label::after {
    top: 50%;
    bottom: inherit;
    transform: translateY(-50%);
  }
}

@media (max-width: 575px) {
  .custom-file-label::after {
    position: static;
    left: auto;
    right: auto;
    top: auto;
    bottom: auto;
    display: inline-block;
    margin-top: .5em;
  }
}

.custom-file-input:focus ~ .custom-file-label {
  border-color: #CC3C33;
  box-shadow: none;
}

.custom-file-input:focus ~ .custom-file-label::after {
  border-color: #CC3C33;
}

