$breakpoints:(
        //md: 123px auto
);
@mixin media($name) {

  $breakpoints:() !default;
  $default:(
          min-xs: 0px auto,
          min-sm: 576px auto,
          min-md: 768px auto,
          min-lg: 992px auto,
          min-xl: 1200px auto,
          min-xxl: 1450px auto,
          min-xxxl: 1680px auto,

          max-xs: auto 400px,
          max-sm: auto 575px,
          max-md: auto 767px,
          max-lg: auto 991px,
          max-xl: auto 1199px,
          max-xxl: auto 1449px,
          max-xxxl: auto 1679px,

          sm-md: 576px 767px,
          md-lg: 768px 991px,
          lg-xl: 992px 1199px,
  );


  $point: map-get($breakpoints, $name) or map-get($default, $name);

  @if $point {

    $min : nth($point, 1); //auto
    $max : nth($point, 2); //576px

    @if $min == auto {
      @media (max-width: $max) {
        @content;
      }
    }@else if $max == auto {
      @media (min-width: $min) {
        @content;
      }
    }@else {
      @media (min-width: $min) and (max-width: $max) {
        @content;
      }
    }
  }
}

//介於兩者數值之間
@mixin minmax($minw, $maxw) {
  @media all and (min-width: $minw) and (max-width: $maxw) {
    @content;
  }
}

@mixin min($minw) {
  @media all and (min-width: $minw) {
    @content;
  }
}

@mixin max($maxw) {
  @media all and (max-width: $maxw) {
    @content;
  }
}

