////font-size
@mixin rwd-fontsize($pc-fs,$notebook-fs,$pad-fs,$small-pad-fs,$phone-fs,$pc-lh,$notebook-lh,$pad-lh,$small-pad-lh,$phone-lh) {
  @include max(768px) {
    font-size: $phone-fs;
    line-height: $phone-lh;
  }
  @include min(768px) {
    font-size: $small-pad-fs;
    line-height: $small-pad-lh;
  }
  @include min(992px) {
    font-size: $pad-fs;
    line-height: $pad-lh;
  }
  @include min(1200px) {
    font-size: $notebook-fs;
    line-height: $notebook-lh;
  }
  @include min(1500px) {
    font-size: $pc-fs;
    line-height: $pc-lh;
  }
}

// 滑過
@mixin aHFA {
  &:hover, &:active, &:focus {
    @content;
  }
}

@mixin hover {
  &:hover {
    @content;
  }
}

//自訂滾輪樣式
@mixin scrollstyle($W,$Bgcolor,$borderC,$BgTcolor,$BgHcolor) {
  &::-webkit-scrollbar {
    width: $W;
  }

  &::-webkit-scrollbar-track {
    background-color: $Bgcolor;
    border-left: 1px solid $borderC;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $BgTcolor;
  }

  &::-webkit-scrollbar-thumb:hover {
    background-color: $BgHcolor;
  }
  // http://blog.rx836.tw/blog/css-styling-scrollbars/
}

// css箭頭樣式
@mixin arrow {
  //display: block;
  //width: 10px;
  //height: 10px;
  //border-width: 2px 2px 0 0;
  border-style: solid;
  @include transition(all 0.3s ease);
  @content;
}

// css三角形
@mixin triangle($bw-t,$bw-r,$bw-b,$bw-l,$bc-t,$bc-r,$bc-b,$bc-l) {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: $bw-t $bw-r $bw-b $bw-l;
  border-color: $bc-t $bc-r $bc-b $bc-l;
  @content;
}

///////////////////////////////////////////////
@mixin verticallr {
  writing-mode: tb-lr;
  -ms-writing-mode: tb-lr;
  -webkit-writing-mode: vertical-lr;
  -moz-writing-mode: vertical-lr;
  -ms-writing-mode: vertical-lr;
  writing-mode: vertical-lr;
  display: inline-block;
  @content;
}

@mixin verticalrl {
  writing-mode: tb-rl;
  -ms-writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  -moz-writing-mode: vertical-rl;
  -ms-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
  display: inline-block;
  @content;
}


