/////////////////////////
////全站使用
/////////////////////////
main {
  overflow: hidden; //防止其他區塊有超出範圍的設定
}

/////////////////////////
////wrap與wrap-xl獨立使用
/////////////////////////

%wrap {
  width: $wrap-xs;
  margin-left: auto;
  margin-right: auto;
  @include min-md {
    width: $wrap-md;
  }
  @include min-xl {
    //width: $wrap-lg;
  }
}

.wrap {
  @extend %wrap;
  max-width: $width;
}

/////////////////////////
////wrap靠左靠右
/////////////////////////

//算出單邊間距+wrap的基本寬度
%wrap-cus-width-xs {
  width: calc((100% - #{$wrap-xs}) / 2 + #{$wrap-xs});
}

%wrap-cus-width-md {
  @include min-md {
    width: calc((100% - #{$wrap-md}) / 2 + #{$wrap-md});
  }
}

%wrap-cus-maxWidth {
  max-width: calc((100% - #{$width}) / 2 + #{$width});
}

//wrap靠左
.wrap-cus-l {
  @extend %wrap-cus-width-xs;
  @extend %wrap-cus-width-md;
  @extend %wrap-cus-maxWidth;
  margin-right: auto;
}

//wrap靠右
.wrap-cus-r {
  @extend %wrap-cus-width-xs;
  @extend %wrap-cus-width-md;
  @extend %wrap-cus-maxWidth;
  margin-left: auto;
}

////max////////////
///////////////////
//wrap靠左
.wrap-cus-l-max-xl {
  @include max-xl {
    width: calc((100% - #{$wrap-md}) / 2 + #{$wrap-md});
  }
  @include max-md {
    width: calc((100% - #{$wrap-xs}) / 2 + #{$wrap-xs});
  }
  margin-right: auto;
  margin-left: unset;
}

//wrap靠右
.wrap-cus-r-max-xl {
  @include max-xl {
    width: calc((100% - #{$wrap-md}) / 2 + #{$wrap-md});
  }
  @include max-md {
    width: calc((100% - #{$wrap-xs}) / 2 + #{$wrap-xs});
  }
  margin-left: auto;
  margin-right: unset;
}

/////////////////////////
////wrap滿版
/////////////////////////

//滿版間距
$wrapfluid: 5vw;
$wrapfluid-xl: 3vw;

.wrap-fluid {
  width: calc(100% - #{$wrapfluid} * 2);
  margin-left: auto;
  margin-right: auto;

  //@include min-xl {
  //  width: calc(100% - #{$wrapfluid-xl} * 2);
  //}
}

//wrap滿版靠左
.wrap-fluid-cus-l {
  width: calc(100% - #{$wrapfluid} * 1.5);
  margin-right: auto;
}

//wrap滿版靠右
.wrap-fluid-cus-r {
  width: calc(100% - #{$wrapfluid} * 1.5);
  margin-left: auto;
}

/////////////////////////
////wrap手機才有
/////////////////////////
.wrap-max-xl-visible {
  @include max-xl {
    width: $wrap-md;
    margin-left: auto;
    margin-right: auto;
  }
}

.wrap-max-lg-visible {
  @include max-lg {
    width: $wrap-md;
    margin-left: auto;
    margin-right: auto;
  }
}

.wrap-max-md-visible {
  @include max-md {
    width: $wrap-xs;
    margin-left: auto;
    margin-right: auto;
  }
}

.wrap-max-sm-visible {
  @include max-sm {
    width: $wrap-xs;
    margin-left: auto;
    margin-right: auto;
  }
}

.wrap-max-xl-visible, .wrap-max-lg-visible {
  @include max-md {
    width: $wrap-xs;
  }
}

.w100-max-sm {
  @include max-sm {
    width: 100%;
  }
}

.w100-max-md {
  @include max-md {
    width: 100%;
  }
}

.w100-max-lg {
  @include max-lg {
    width: 100%;
  }
}

.w100-max-xl {
  @include max-xl {
    width: 100%;
  }
}

.w100-max-xxl {
  @include max-xxl {
    width: 100%;
  }
}

//////////////////////
.no-desc {
  @extend %no-desc;
}

.desc-default {
  @extend %desc-default;
}


//////////////////////
//picturefill-background
//用在首頁情境圖
//////////////////////
.js-fillbg {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  //height: 100%; //會造成增加空間
}


