/* 網站基礎設定
==================================*/
* {
  padding: 0;
  margin: 0;
  //cursor: none !important;
}

html, body {
  font-size: $base-font-size;
}

body {
  margin: 0 auto;
  color: $color-title;
  line-height: 1.5;
  @extend %font-all;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @include min-xl {
    line-height: 2;
  }

  //反白設定
  ::selection {
    background: $color-primary;
    color: #fff;
  }

  img::selection {
    background: transparent;
  }

}

//remove chrome focus blue line
*:focus {
  outline: none;
}

a {
  color: inherit;
  transition: all .3s ease;

  &:hover {
    color: $color-primary;
  }

  &:hover,
  &:active,
  &:focus {
  }
}

u {
  text-decoration: none;
  color: $color-primary;
  min-width: 5px;
  display: inline-block;
  padding: 2px 0 0 5px;
  margin: 0;
  line-height: 100%;
  vertical-align: text-top;
}

b, strong {
  display: inline-block;
}

ol, ul {
  li {
  }
}

ul {
  > li {
    list-style: none;
    display: table;
    table-layout: fixed;

    &:before {
      content: '・';
      display: table-cell;
      vertical-align: top;
      //width: 1em;
      //margin-right: .5em;

      //line-height: .8;
      //
      //@include min-xl {
      //  line-height: 1;
      //}

      @include min-xl {
        width: 1em;
      }
    }

    img {
      vertical-align: middle;
    }
  }
}

ol {
  //margin-left: 1.1em; //數字
  margin-left: 1.5em; //國字：一、二...

  li {
    //list-style-type: decimal;
    list-style-type: cjk-ideographic;
  }

  img {
    vertical-align: middle;
  }

  ul {
    margin: 1em 0;
    li {
      list-style-type: none;
    }
  }
}

li {

  & + li {
    margin-top: .5em;
  }

  > p {
    margin-top: 0;
  }
}

hr {
  border: 0;
  height: 1px;
  background-color: $color-border;
  color: #d4d4d4; /* IE6 */
  margin-top: 10px;
  margin-bottom: 10px;
}

div, p, li, a, span, i, em, strong {
  //@extend .fz-default;
}

p {
  & + p {
    margin-top: .5em;
  }
}

* + p {
  margin-top: 1em;
}


table {
  max-width: 100%;
  th, td {
    padding: 1em 1em .85em 1em;
    border: 1px solid $color-table;
  }
}

hr {
  margin-top: 1.5em;
  margin-bottom: 1.5em;

  @include min-xl {
    margin-top: 2.5em;
    margin-bottom: 2.5em;
  }
}

blockquote {
  border-left: 5px solid $color-primary;
  padding-left: 1.5em;

  @include min-xl {
    padding-left: 2em;
  }
}

////////////////////////////////////////////////////
//                                                //
//                  font-size                     //
//                                                //
////////////////////////////////////////////////////

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  line-height: 1.3;
  margin: 0;

  @include min-md {
    line-height: 1.2;
  }
}

h1 {
  @extend .fz-h1;
}

h2 {
  @extend .fz-h2;
}

h3 {
  @extend .fz-h3;
}

h4 {
  @extend .fz-h4;
}

h5 {
  @extend .fz-h5;
}

h6 {
  @extend .fz-h6;
}


